.SideDrawer__SideDrawer__hash-base64-5 {
    position: fixed;
    width: 210px;
    max-width: 50%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: white;
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform 0.3s ease-out;
}

@media (min-width: 500px) {
    .SideDrawer__SideDrawer__hash-base64-5 {
        display: none;
    }
}

.SideDrawer__Open__hash-base64-5 {
    transform: translateX(0);
}

.SideDrawer__Close__hash-base64-5 {
    transform: translateX(-100%);
}

.SideDrawer__Logo__hash-base64-5 {
    height: 11%;
    margin-bottom: 32px;
}